import React from 'react';
import './App.css';
import axios from 'axios';

class App extends React.Component{
  constructor(){
    super();
    this.state = {
      logs: [],
      reverses: {},
      links: {}
    }
  }

  componentDidMount(){
    axios.get('https://nanaimg.net/logs')
      .then(res => {
        let arr = res.data.response;
        let rev = [];
        let links = [];
        arr.forEach((ar) => {
          rev = {
            ...rev,
            [ar.action_id]: false
          }
          links = {
            ...links,
            [ar.action_id]: ar.recipient_url
          }
        })
        this.setState({
          ...this.state,
          logs: [...arr],
          reverses: rev,
          links: links
        })
      })
      .catch(err => {
        console.log(err);
      })
  }

  goToContent = (e) => {
    let id = e.target.id.split('-')[1];
    window.open(this.state.links[id]);
  }

  reverseAction = (e) => {
    let id = e.target.id.split('-')[1];
    let contentId = e.target.id.split('-')[2];
    let action = document.querySelector(`#action-${id}`).textContent.split(' ')[0];
    let target = document.querySelector(`#action-${id}`).textContent.split(' ')[1];
    if (action === 'removed'){
      if (target === 'image'){
        axios.post(`https://nanaimg.net/logs/restore/image/${contentId}/${id}`);
      } else if (target === 'manifesto'){
        axios.post(`https://nanaimg.net/logs/restore/manifesto/${contentId}/${id}`);
      } else if (target === 'comment'){
        axios.post(`https://nanaimg.net/logs/restore/comment/${contentId}/${id}`);
      }
    } else if (action === 'restored'){
      if (target === 'image'){
        axios.post(`https://nanaimg.net/logs/remove/image/${contentId}/${id}`);
      } else if (target === 'manifesto'){
        axios.post(`https://nanaimg.net/logs/remove/manifesto/${contentId}/${id}`);
      } else if (target === 'comment'){
        axios.post(`https://nanaimg.net/logs/remove/comment/${contentId}/${id}`);
      }
    }
    this.setState({
      ...this.state,
      reverses: {
        ...this.state.reverses,
        [id]: true
      }
    })
  }

  render(){
    return(
      <>
      <h1 id="h1-mod-logs">Mod Logs</h1>
      <div id="logs-heading">
          <h2 className="logs-types regl-user">User</h2>
          <h2 className="logs-types regl-action">Action</h2>
          <h2 className="logs-types regl-id">Content ID</h2>
          <h2 className="logs-types regl-date">Date</h2>
          <h2 className="logs-types regl-reason">Reason</h2>
          <h2 className="logs-types regl-actions">Actions</h2>
        </div>
      {this.state.logs.map((log) => {
        return(
          <div key={log.action_id} className="log-cards">
            <h2 className="log-data regl-user">{log.user}</h2>
            <h2 id={`action-${log.action_id}`} className="log-data regl-action">{log.action}</h2>
            <h2 className="log-data regl-id">{log.recipient}</h2>
            <h2 className="log-data regl-date">{log.date}</h2>
            <h2 className="log-data regl-reason">{log.explanation}</h2>
            <div className="regl-actions">
              {(this.state.reverses[log.action_id] === false) ? 
              <>
              <button className="logger-buttons" id={`goto-${log.action_id}`} onClick={this.goToContent}>Go to Content</button>
              <button className="logger-buttons" id={`reverse-${log.action_id}-${log.recipient}`} onClick={this.reverseAction}>Reverse</button></> :
              <h3 id="h3-log-reversed">Action Reversed</h3>
              }
            </div>
          </div>
        )
      })}
      </>
    )
  }
}

export default App;
